.stories-container {
  background-color: white;
  border: solid 1px var(--grey-219);
  border-radius: 5px;
  width: 610px;
  padding: 10px 14px;
  display: flex;
  flex-direction: row;
}

.story-profile {
  padding-left: 14px;
}

.profile-outer-circle {
  cursor: pointer;
  height: 66px;
  width: 66px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  border: 2px solid #d3378b;
  text-align: center;
}

.profile-circle {
  height: 94%;
  width: 94%;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid var(--grey-219);
  text-align: center;
  margin: 3% 3%;
}

.profile-circle img {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}

.profile-name {
  margin-top: 2px;
  text-align: center;
  font-weight: 500;
  max-width: 66px;
  max-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.profile-name span {
  font-size: 0.75rem;
}

.profile-small {
  height: 33px !important;
  width: 33px !important;
}

.post {
  background-color: white;
  margin-top: 24px;
  border: solid 1px var(--grey-219);
  border-radius: 5px;
}

.post-header {
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.profile-link {
  margin-left: 10px;
}

.profile-link a {
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 550;
  color: rgb(38, 38, 38);
}

.profile-link a:hover {
  text-decoration: underline;
}

.post-header-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.elipsis-icon {
  cursor: pointer;
  transform: rotate(90deg);
}

.posts-container {
  margin-bottom: 20px;
}

.post-media {
  width: 610px;
  height: auto;
}

.post-media img {
  width: 100%;
  height: auto;
}

.post-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.post-buttons-left {
  padding-left: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.post-icon {
  cursor: pointer;
  font-size: 160%;
  margin-right: 16px;
}

.post-likes {
  margin: 14px 16px;
  font-weight: 600;
  font-size: 0.9rem;
}

.post-comments {
  padding-left: 16px;
  font-size: 0.9rem;
}

.post-comment-user {
  font-weight: 600;
  margin-right: 5px;
}

.view-all-comments {
  margin: 8px 0;
  color: rgb(143, 143, 143);
}

.post-comment {
  margin: 4px 0;
}

.post-date {
  margin: 6px 0;
  padding-left: 16px;
  font-size: 0.65rem;
  font-weight: 500;
  color: rgb(143, 143, 143);
}

.post-comment-field {
  border-top: 1px solid var(--grey-219);
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 16px;
  display: flex;
  align-items: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.post-comment-field-left {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.post-comment-field input {
  padding: 6px 10px;
  font-size: 0.9rem;
  border: none;
  width: 100%;
}

.input-comment-div {
  width: 100%;
}

.post-comment-field input:focus {
  outline: none;
}

.post-comment-btn-div .blue-btn {
  margin-right: 8px;
}

.blue-btn {
  cursor: pointer;
  border: none;
  color: rgb(0, 149, 246);
  font-weight: 600;
  font-size: 0.9rem;
  text-decoration: none;
}

/* ============================ */

main .center-middle {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100%;
}

.main-right {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: 30px;
  padding-top: 30px;
  min-height: 600px;
  min-width: 320px;
  overflow-y: hidden;
}
.profile-view {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* background-color: burlywood; */
  width: 100%;
}
.profile-view-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.user-profile-image {
}
.user-profile-info {
  display: flex;
  flex-direction: column;
  margin-left: 16px;
}
.user-profile-username a {
  font-size: 0.85rem;
  font-weight: 600;
  color: #494949;
  text-decoration: none;
  padding-left: 2px;
}
.user-profile-name {
  color: rgb(143, 143, 143);
  font-size: 0.9rem;
  font-weight: 500;
  padding-top: 2px;
}
.profile-view-right {
}
.profile-view-right .blue-btn {
  font-size: 0.75rem;
}

.follow-suggestions {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 30px;
}

.follow-suggestions-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 18px;
}

.follow-suggestions-text {
  font-size: 0.85rem;
  font-weight: 600;
  color: rgb(143, 143, 143);
}

.follow-suggestions-all a {
  text-decoration: none;
  font-size: 0.75rem;
  font-weight: 600;
  color: #494949;
}

.follow-suggestion-item {
  padding-left: 8px;
  margin-bottom: 12px;
}

.name-small {
  font-size: 0.75rem;
}

.follow-suggestion-item .user-profile-username a:hover {
  text-decoration: underline;
}

.nav-links {
  display: flex;
  flex-wrap: wrap;
  max-width: 300px;
}

.nav-links {
  margin-top: 30px;
  margin-bottom: 20px;
}

.nav-link:not(:last-of-type)::after {
  content: '\00B7';
  color: rgb(190, 190, 190);
  margin: 0 0.25em 0 0.25em;
}

.nav-link a {
  text-decoration: none;
  color: rgb(190, 190, 190);
  font-size: 0.7rem;
}

.copyright-statement {
  color: rgb(190, 190, 190);
  font-size: 0.65rem;
}

/* hide right column at >1000px */
@media (max-width: 999px) {
  .main-right {
    display: none;
  }
}

/* hide comment feature at 735px, since instagram does so */
@media (max-width: 735px) {
  .post-comment-field {
    display: none;
  }
}

/* switch to responsive mobile view at >640px */

@media (max-width: 639px) {
  main .center-middle {
    margin-top: 2px;
    overflow-x: hidden;
  }

  .post {
    border: none;
    background-color: var(--grey-250);
  }

  .post-media {
    width: 98vw;
  }

  .stories-container {
    width: 98vw;
    border-top: none;
    border-left: none;
    border-right: none;
    border-radius: 0;
    background-color: var(--grey-250);
  }

  .main-left {
    width: 100vw;
  }
}
